@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.gpt3__blog-container_article {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.gpt3__blog-container_article-image {
  width: 100%;
  height: 100%;
  /* background: var(--color-footer); */
}
.gpt3__blog-container_article-image img {
  width: 100%;
  height: 100%;
}
.gpt3__blog-container_article-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 1rem 1.5rem;
  height: 100%;
  background: var(--color-footer);
}
.gpt3__blog-container_article-content h3 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 19.3154px;
  line-height: 30px;
  color: #ffffff;
  margin-bottom: 5rem;
  cursor: pointer;
}
.gpt3__blog-container_article-content p,
.gpt3__blog-container_article-content a {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 11.649px;
  line-height: 35px;
  color: #ffffff;
}
@media screen and (max-width: 550px) {
    .gpt3__blog-container_article-content h3 {
        font-size: 18px;
        line-height: 25px;
    }
}
.gpt3__brand {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.gpt3__brand img {
  margin: 1rem;
  max-width: 150px;
  min-width: 120px;
}

.gpt3__cta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  margin: 4rem;
  background: var(--gradient-bar);
  border: 1px solid #000000;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10.7236px;
}
.gpt3__cta-content p {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 12px;
  line-height: 30px;
  color: #0e0e0e;
}
.gpt3__cta-content h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 24px;
  line-height: 45px;
  color: #000000;
}
.gpt3__cta-btn{
    display: flex;
    justify-content: center;
    align-items: center; 
    margin-left: 2rem;
}
.gpt3__cta-btn button {
    font-family: var(--font-family);
  background: #000000;
  border-radius: 40px;
  font-weight: 700;
  font-size: 18px;
  line-height: 30px;
  color: #ffffff;
  padding: 0.5rem 1rem;
  border: none;
  outline: none;
  cursor: pointer;
  min-width: 150px;
}
@media screen and (max-width: 700px) {
    .gpt3__cta{
      flex-direction: column;
    }
  
    .gpt3__cta-btn {
      margin: 2rem 0 0;
    }
  }
  @media screen and (max-width: 550px) {
    .gpt3__cta{
        margin: 4rem 2rem;
    }
  
    .gpt3__cta-content h1{
      font-size: 18px;
      line-height: 32px;
    }
    .gpt3__cta-btn button {
        font-size: 14px;
        line-height: 28px;
      }
  }
.gpt3__features-container__feature {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: row;
  margin: 1rem ;
}
.gpt3__features-container__feature-title {
  flex: 1 1;
  max-width: 280px;
  margin-right: 2rem;
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 16px;
  line-height: 75px;
  letter-spacing: -0.04em;
  color: #ffffff;
}
.gpt3__features-container__feature-title h1{
    margin: 0 2rem 2rem 0;
}
.gpt3__features-container__feature-title div {
  width: 38px;
