  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  margin-bottom: 0.25rem;
}
.gpt3__features-container__feature-text {
  flex: 2 1;
  max-width: 390px;
  display: flex;
}
.gpt3__features-container__feature-text p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-text);
}
@media screen and (max-width: 550px) {
    .gpt3__features-container__feature-title{
        font-size: 14px;
        line-height: 22px;
    }  
    .gpt3__features-container__feature-text{
        font-size: 12px;
        line-height: 20px; 
    }
    .gpt3__features-container__feature{
        margin: 1rem 0;
    }
}
.gpt3__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 6rem;
}
.gpt3__navbar-links {
  flex: 1 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.gpt3__navbar-links_logo {
  margin-right: 2rem;
}
.gpt3__navbar-links_logo img {
  height: 16.02px;
  width: 62.56px;
}
.gpt3__navbar-links_container {
  display: flex;
  flex-direction: row;
}
.gpt3__navbar-sign {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.gpt3__navbar-links_container p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu_container p {
  color: #ffff;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  text-transform: capitalize; /* make first letter Capital */
  margin: 0 1rem;
  /*cursor: pointer;*/
}
.gpt3__navbar-sign button,
.gpt3__navbar-menu_container button {
  padding: 1rem 2rem;
  color: #ffff;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  border: 0;
  cursor: pointer;
  background: #ff4820;
  border-radius: 5px;
  outline: none;
}
.gpt3__navbar-menu{
    margin-left: 1rem;
    display: none;
    position: relative;
}
.gpt3__navbar-menu svg {
    cursor: pointer;
}
.gpt3__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items:  flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}
.gpt3__navbar-menu_container p {
    margin:  1rem 0;
}
.gpt3__navbar-menu_container-link-sign{
    display: none;
}
@media screen and (max-width: 1050px) {
    .gpt3__navbar-links_container{
        display: none;
    }
    .gpt3__navbar-menu{
       display: flex; 
    }
}
@media screen and (max-width: 700px) {
    .gpt3__navbar{
        padding: 2rem 4rem;
    }
}
@media screen and (max-width: 550px) {
    .gpt3__navbar{
        padding: 2rem;
    }
    .gpt3__navbar-sign {
        display: none;
    }
    .gpt3__navbar-menu_container{
        top: 20px;
    }
    .gpt3__navbar-menu_container-link-sign{
        display: block;
    }
}
.gpt3__blog {
  display: flex;
  flex-direction: column;
}
.gpt3__blog-heading {
  width: 100%;
  text-align: left;
  margin-bottom: 5rem;
}
.gpt3__blog-heading h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 62px;
  line-height: 75px;
  letter-spacing: -0.04em;
}
