  display: flex;
  flex-direction: row;
}
.gpt3__blog-container_groupA {
  flex: 0.75 1;
  margin-right: 2rem;
}
.gpt3__blog-container_groupB {
  flex: 1 1;
  /* margin-right: 2rem; */
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
}
